import uniNavBar from '../../compoents/uni-nav-bar/uni-nav-bar.vue';
import uploadImage from '../../compoents/common/upload-image.vue';
import uniPopup from '../../compoents/uni-popup/uni-popup.vue';
let arry=['所有人可见','仅自己可见']
var sourceType = [
	    ['camera'],
	    ['album'],
	    ['camera', 'album']
]
var sizeType = [
	    ['compressed'],
	    ['original'],
	    ['compressed', 'original']
]
export default {
	components:{
		uniNavBar,
		uploadImage,
		uniPopup
	},
	onBackPress(ev){
		if(this.text==''&&this.imageList.length==0){return;}
		if(this.fanhui){
			uni.showModal({
			    content: '是否保存',
			    success:  (res)=> {
			        if (res.confirm) {
			            console.log('保存');
			        } else if (res.cancel) {
			            console.log('不保存');
			        }
					this.fanhui=false;
					uni.navigateBack({
						delta:1
					})
			    }
			});
			return true;
		}
			
	},
	data() {
		return {
			fanhui:true,
			donghua:'animated zoomInDown',
			show:true,
			yinsi:'所有人可见',
			text:'',
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9]
		}
	},
	methods: {
		ttt(){
			this.donghua='animated zoomInDown'
			this.show=false
		},
		deleted(index){
			uni.showModal({
			    title: '提示',
			    content: '是否删除',
			    success: (res) =>{
			        if (res.confirm) {
			           this.imageList.splice(index,1)
			        } 
			    }
			});
		},
		chooseImage: async function() {









		
		    if (this.imageList.length === 9) {
		        let isContinue = await this.isFullImg();
		        console.log("是否继续?", isContinue);
		        if (!isContinue) {
		            return;
		        }
		    }
		    uni.chooseImage({
		        sourceType: sourceType[this.sourceTypeIndex],
		        sizeType: sizeType[this.sizeTypeIndex],
		        count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length :
		            this.count[this.countIndex],
		        success: (res) => {
		            this.imageList = this.imageList.concat(res.tempFilePaths);
		        },
		        fail: (err) => {





		        }
		    })
		},
		previewImage: function(e) {
			console.log(e); 
		    var current = e.target.dataset.src
		    uni.previewImage({
		        current: current,
		        urls: this.imageList,
				indicator:'default'   
		    })
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		submit(){
			
		},
		changeyinsi(){
			uni.showActionSheet({
			    itemList: arry,
			    success:(res)=> {
			     //   console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					this.yinsi=arry[res.tapIndex];
			    }
			});
		}
	
	}
}
